.spotcard-container {
    border-radius: 24px;
    background: #19191B;
}

.spotcard-container > .spotcard-cover {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 180px;
    background-image: url('https://rockymountevents.com/wp-content/uploads/2019/06/Concert-crowd-with-silhouettes-of-people-hold-their-hands-up.jpg');
    background-size: cover;
    background-position: bottom center;
    border-radius: 24px 24px 0 0;
}

.spotcard-container > .spotcard-cover > .spotcard-cover-dark {
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) -7.41%, rgba(0, 0, 0, 0.5) 100%);
    position: absolute;
}


.spotcard-title {
    position: relative;
    font-size: 36px;
    font-weight: 700;
    line-height: 1.1em;
    z-index: 99;
    margin-left: 16px;
    margin-bottom: 20px;
}

.spotcard-inforow {
    padding: 2px 4px;
}

.spotcard-info-title {
    padding: 0;
    margin: 0;
    line-height: 1em;
}

.spotcard-info-title.description {
    font-size: 13px;
    color: #909499;
    padding-top: 4px;
}